* {
    font-family: Arial, Helvetica, sans-serif;
}
body {
    padding: 60px;
}
input {
    width: 100%;
    font-size: 1.2rem;
    color: grey;
}
.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.card {
    align-content: center;
    border: 1px solid grey;
    overflow: hidden;
    margin-bottom: 10px;
    margin-right: 10px;
    border-radius: 6px;
}
.card p {
    padding: 5px;
    text-align: center;
}
.card img {
    align-content: center;
    max-height: 170px;
}
